import React from "react";
import Layout from '../../components/Global/Layout/Layout'
import Header from '../../components/Global/App/Header/Header.component'
import Breadcrumb from '../../components/Global/Pages/Breadcrumb'
import FaqSection from '../../components/Local/Pages/FAQ/faq.component'
import StartPartners from '../../components/Global/StartPartners/StartPartners.component'
import Footer from '../../components/Global/App/Footer/Footer.component'

const faq = () => {
	return (	
		<Layout>
        <Header />
		<Breadcrumb pageName='Faq'/>
		<FaqSection />
		<StartPartners addClass="bgc-thm"/>
        <Footer />
		</Layout>
	);
}

export default faq